body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #ededed;
}

.logo {
  max-width: 60%;
  height: auto;
}

.navbar {
  background: #d400aa;
}

.navbar-brand {
  color: #fff;
}

.footer {
  background: #343a40;
  position: fixed;
  bottom: 0;
  width: 100%;
}

.search-button {
  background: #ff7f2a;
  color: #000;
}

.nav-link {
  color: #fff;
}

.card-header {
  background: #ff7f2a;
}

